(function() {
  angular.module("exampleApp", ['ui.router']);

}).call(this);

(function() {
  angular.module("exampleApp").config([
    "$stateProvider", "$urlRouterProvider", function($stateProvider, $urlRouterProvider) {
      $urlRouterProvider.otherwise('/home');
      return $stateProvider.state('home', {
        url: '/home',
        templateUrl: 'templates/home.html',
        controller: 'FirstCtrl'
      }).state('home.list', {
        url: '/list',
        templateUrl: 'templates/home-list.html',
        controller: 'HomeListCtrl'
      }).state('home.paragraph', {
        url: '/paragraph',
        template: 'I could sure use a drink right now'
      }).state('about', {
        url: '/about',
        views: {
          '': {
            templateUrl: 'templates/about.html'
          },
          'columnOne@about': {
            template: 'Column 1'
          },
          'columnTwo@about': {
            template: 'Column 2'
          }
        }
      });
    }
  ]);

}).call(this);

(function() {
  var ctrl;

  ctrl = function($scope) {
    return $scope.products = [
      {
        name: "Apples",
        category: "Fruit",
        price: 1.20,
        expiry: 10
      }, {
        name: "Bananas",
        category: "Fruit",
        price: 2.42,
        expiry: 7
      }, {
        name: "Pears",
        category: "Fruit",
        price: 2.02,
        expiry: 6
      }
    ];
  };

  angular.module("exampleApp").controller("FirstCtrl", ["$scope", ctrl]);

}).call(this);

(function() {
  var ctrl;

  ctrl = function($scope) {
    return $scope.dogs = ['Bernese', 'Husky', 'Goldendoodle'];
  };

  angular.module("exampleApp").controller("HomeListCtrl", ["$scope", ctrl]);

}).call(this);

(function() {
  /** @jsx React.DOM */;
  angular.module("exampleApp").directive("reactTest", function() {
    return function(scope, element, attrs) {
      var Hello;
      Hello = React.createClass({displayName: "Hello",
        render: function() {
          return React.createElement("div", null, "Hello World");
        }
      });
      return React.render(React.createElement(Hello, null), element[0]);
    };
  });

}).call(this);

(function() {
  angular.module("exampleApp").directive("unorderedList", function() {
    return function(scope, element, attrs) {
      var data, i, item, len, listElem, propertyName, results;
      data = scope[attrs["unorderedList"]];
      propertyName = attrs["listProperty"];
      if (angular.isArray(data)) {
        listElem = angular.element("<ul>");
        element.append(listElem);
        results = [];
        for (i = 0, len = data.length; i < len; i++) {
          item = data[i];
          results.push(listElem.append(angular.element('<li>').text(item[propertyName])));
        }
        return results;
      }
    };
  });

}).call(this);
